<template>
  <div id="AddBlog">
    <h1>写博客</h1>
    <form v-if="!submmited">
      <label>主题:</label>
      <input type="text" v-model="blog.title">
      <label>内容:</label>
      <textarea v-model="blog.content"></textarea>
      <label >分类:</label>
      <div class="checkboxs">
        <label>vue.js</label>
        <input type="checkbox" value="vue.js" v-model="blog.arr">
        <label>react.js</label>
        <input type="checkbox" value="react.js" v-model="blog.arr">
        <label>anguler.js</label>
        <input type="checkbox" value="anguler.js" v-model="blog.arr">
        <label>node.js</label>
        <input type="checkbox" value="node.js" v-model="blog.arr">
      </div>
      <label>作者：</label>
      <select v-model="blog.author">
        <option v-for="author in authors">
          {{author}}
        </option>
      </select>
    </form>
    <button v-on:click.prevent="ad">添加博客</button>

    <div class="preview">
      <h3>博客总览</h3>
      <p>博客主题:{{blog.title}}</p>
      <p>博客内容:{{blog.content}}</p>
      <p>分类:</p>
      <ul>
        <li v-for="ar in blog.arr">{{ar}}</li>
      </ul>
      <p>作者:{{blog.author}}</p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AddBlog',
  data () {
    return {
      blog :{
         title:'',
         content:'',
         arr:[],
         author:''
      },
      authors:['小文','小张','小李'],
      submmited:false
     
    }
  },
  methods:{
    ad:function () {
      this.$http.post("https://wd6439573863iggxjt.wilddogio.com/posts.json",this.blog)
      .then(function(){
        alert('添加成功');
        this.submmited = true;
      });
    }
  }
}
</script>

<style scoped>
#AddBlog *{
  /* 让这个盒子包含的所有内容以box形式存在 */
   box-sizing: border-box;
}
#AddBlog{
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
  background: thistle;
  opacity: 0.7;
  border: 1px #cccccc dotted;
  
}
label{
  display: block;
  margin: 20px  0 10px;
}
input[type="text"],textarea,select{
  display: block;
  width: 100%;
  padding: 10px;
}
.checkboxs label{
  display: inline-block;
  margin-top:0;
  margin-right: 10px; 
}
.checkboxs input{
  display: inline-block;

}
button{
  padding: 10px;
  background: skyblue;
  color: brown;
  font-weight: 700;
  border-radius: 7px;
  border:none;
  cursor: pointer;
  margin: 10px;
}
.preview{
  padding: 10px 10px;
  border: 1px dotted red;
}
h3{
  margin-top: 10px;
}
h1{
  color: brown;
  }


</style>



