<template>
   <!-- binging传的参数需要‘’引起来 -->
  <div v-theme="'wide'" id="showblog" >
    <h1>博客展览</h1>
    <input type="text" v-model="search" placeholder="search">
    <div v-for="blog in filterBlogs" class="ex-blog">
      <router-link v-bind:to="'/blog/'+ blog.id"><h2 v-rainbow>
          {{blog.title | to-upperCase}}
      </h2></router-link> 
        <article>
          {{blog.content}}</article>
    </div>
  </div>
</template>

<script>

export default {
  name: 'showblog',
  data() {
      return {
        blogs:[],
        search:''
      }
  },
  created(){
    // 请求本地文件只能放在static里面
      this.$http.get('https://wd6439573863iggxjt.wilddogio.com/posts.json')
      .then(function(data){
        // this.blogs = data.body.slice(0,10);
        // console.log(data);
        return data.json();
      }).then(function(data){
        var blogsArr = [];
        for(let key in data){
          data[key].id = key;
          blogsArr.push(data[key]);
        }
        // console.log(blogsArr);
        this.blogs = blogsArr;
      })
  },
  directives:{
    'rainbow':{
      bind(el,binding,vnode){
        el.style.color = "#" + Math.random().toString(16).slice(4,10);
      }
    }
  }
  ,
  filters:{
    'to-upperCase':function(value){
       return value.toUpperCase();
    }
  },
  // filters:{
  //     'snippet':function(value){
  //      return value.slice(0,100) + '...';
  //     }
  //   }
  // ,
  computed:{
    filterBlogs:function(){
      return this.blogs.filter((blog) =>{
        return blog.title.match(this.search);
        // es3方法，用blog.title和search匹配相同的返回给
        // filterBlogs 然后到上面进进行遍历并显示
      })
    }
  }
}
</script>

<style scoped>
#showblog{
  max-width: 1200px;
  margin: 0 auto;
  background:thistle
}
.ex-blog{
  padding: 20px;
  margin:20px;
  background:#eee;
  box-sizing: border-box；
}
input{
  margin-left: 20px;
  height: 50px;
  width: 600px;
  border-radius: 3px;
  border: 3px seagreen dotted;
  background: #eee;
}
h1{
  color:teal;
  margin-left: 20px;
}
a{
  text-decoration: none;
}
</style>


