<template>
    <div class="single-blog">
     <div class="md">
        <h1>主题：{{blog.title}}</h1>
        <article>内容：{{blog.content}}</article>
        <p>作者：{{blog.author}}</p>
        <ul>
            <li v-for="ar in blog.arr">分类：{{ar}}</li>
        </ul>
     </div>
    </div>
</template>
<script>
    export default{
        name:'single-blog',
        data(){
            return {
               id:this.$route.params.id,
               blog:{}
            }
        },
        created(){
            this.$http.get('https://wd6439573863iggxjt.wilddogio.com/posts/' + this.id +".json")
            .then(function(data){
                // console.log(data);  
                return data.json();
                // this.blog = data.body; 
            })
            .then(function(data){
                this.blog = data;
            })
        }
    }
</script>
<style>
#single-blog{
    position: relative;
    top: 100px;
    background: thistle;
}
.md{
    position: absolute;
    width: 1000px;
    top: 100px;
    padding: 20px;
    background: #ccc;
    border: 1px dotted steelblue;
}
li{
    list-style: none;
    margin-left: -40px;
}
</style>



